import { Component, Prop, h, Event, EventEmitter, State, Watch } from "@stencil/core";





@Component({
    tag: 'jji-guest-details',
    styleUrl: './guest-details.css',
})
export class GuestDetails {

    // State variables to store data and control form visibility
    @State() allCountries: any[] = [];
    @State() responseD: boolean;

    //Controls the visibility of the sign-up form.
    @Prop({ reflect: true, mutable: true }) opened: boolean;

// Event emitter for form submission
    @Event({ bubbles: true, composed: true }) formDataSubmitted: EventEmitter<any>;

    // Prop to store external data passed to the component
    @Prop({ mutable: true, reflect: true }) data: any = null;

    // State variables to store form input values
    @State() firstname: string = '';
    @State() lastname: string = '';
    @State() email: string = '';
    @State() alternateemail: string = '';
    @State() password: string = '';
    @State() country: string = '';
    @State() city: string = '';
    @State() adress: string = '';
    @State() countrycode: string = '';
    @State() mobilenumber: string = '';
    @State() newsletter: boolean = false;
    // @State() currency: string = '';
    // @State() language: string = '';

    //Indicates whether the form has been submitted.
    @State() formSubmitted: boolean = false;

    //New state variable to control the error message
    @State() showFormError: boolean = false;


    // Watch for changes to the 'data' prop and update the component state accordingly
    @Watch('data')
    fetchGettedData(newValue: any, oldValue: any) {
        if (this.data !== null && newValue !== oldValue) {
            this.firstname = this.data.My_Result.My_Guest.FIRST_NAME;
            this.lastname = this.data.My_Result.My_Guest.LAST_NAME;
            this.email = this.data.My_Result.My_Guest.My_User.EMAIL;
            this.alternateemail = this.data.My_Result.My_Guest.My_User.DISCLOSED_EMAIL;
            this.password = this.data.My_Result.My_Guest.My_User.PASSWORD;
            this.country = this.data.My_Result.My_Guest.COUNTRY_ID;
            this.city = this.data.My_Result.My_Guest.CITY;
            this.adress = this.data.My_Result.My_Guest.ADDRESS;
            this.mobilenumber = this.data.My_Result.My_Guest.MOBILE;
            this.countrycode = this.data.My_Result.My_Guest.PHONE_PREFIX;
            this.newsletter = this.data.My_Result.My_Guest.IS_NEWS_LETTER;
            // this.currency = this.data.My_Result.My_Guest.My_User.CURRENCY;
            // this.language = this.data.My_Result.My_Guest.My_User.LANGUAGE;
            // console.log(this.currency)
        } else {
            this.firstname = '';
            this.lastname = '';
            this.email = '';
            this.alternateemail = '';
            this.password = '';
            this.country = '';
            this.city = '';
            this.adress = '';
            this.mobilenumber = '';
            this.countrycode = '';
            this.newsletter = false;
            // this.currency = '';
            // this.language = '';
        }
    }

    async componentDidLoad() {
        // Fetch data from the API and update the state with the incoming data
        const dataFromAPI = await this.fetchDataFromAPI();
        if (dataFromAPI) {
            // Update the state with the incoming data
            this.firstname = dataFromAPI.firstname;
            this.lastname = dataFromAPI.lastname;
            this.email = dataFromAPI.email;
            this.alternateemail = dataFromAPI.alternateemail;
            this.password = dataFromAPI.password;
            this.country = dataFromAPI.country;
            this.city = dataFromAPI.city;
            this.adress = dataFromAPI.adress;
            this.countrycode = dataFromAPI.countrycode;
            this.mobilenumber = dataFromAPI.mobilenumber;
            this.newsletter = dataFromAPI.newsletter;
        }
    }

    // Fetch data from the API to set 'allCountries'.
    async fetchDataFromAPI() {
        try {
            const myHeaders = new Headers();
            myHeaders.append("Content-Type", "application/json");

            const raw = JSON.stringify({
                "language": "EN"
            });

            const requestOptions: RequestInit = {
                method: 'POST',
                headers: myHeaders,
                body: raw,
                redirect: 'follow'
            };

            const response = await fetch("https://gateway.igloorooms.com/IR/Get_Exposed_Countries?Ticket=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJleHAiOjE2OTE0ODMwNzcsIkNMQUlNLTAxIjoicktLMi9DY1dQQnM9IiwiQ0xBSU0tMDIiOiI5UStMQm93VTl6az0iLCJDTEFJTS0wMyI6Ilp3Tys5azJoTzUwPSIsIkNMQUlNLTA0IjoiZE5oVHRrc2lQaS9FV29LaXB2TnhkejU1K1VVd2VXc3dqZGlhUDFMYmN2OD0ifQ.t6AUWG-Oq70EnldT5b2zZwjvzQyBESapcZx6uK7wDMI", requestOptions);
            this.responseD = response.ok;
            if (!response.ok) {
                throw new Error('Network response was not ok');
            }
            const data = await response.json();
            this.allCountries = data.My_Result;
            // console.log(this.allCountries);
            console.log('From Country Api Status: '+this.responseD);
        } catch (error) {
            console.error('Error fetching data:', error);
            return null;
        }
    }

    // Event handler for checkbox change
    handleCheckboxChange(event: Event) {
        const checkbox = event.target as HTMLInputElement;
        this.newsletter = checkbox.checked;
    }

    // Event handler for form submission
    async handleSubmit(event: Event) {
        event.preventDefault();
        const formData = {
            firstname: this.firstname,
            lastname: this.lastname,
            email: this.email,
            alternateemail: this.alternateemail,
            password: this.password,
            country: this.country,
            city: this.city,
            adress: this.adress,
            countrycode: this.countrycode,
            mobilenumber: this.mobilenumber,
            newsletter: this.newsletter,
        };

        // Emit the form data using the 'formDataSubmitted' event
        this.formDataSubmitted.emit(formData);
        console.log(formData);
    }


    // Event handler for the save button click
    saveGuestDetailsClick(event: MouseEvent) {
        event.preventDefault();
        if (
            this.firstname.trim() === '' ||
            this.lastname.trim() === '' ||
            this.email.trim() === '' ||
            this.password.trim() === '' ||
            this.country.trim() === '' ||
            this.countrycode.trim() === '' ||
            this.mobilenumber.trim() === ''
        ) {

            this.showFormError = true;
            console.log('Please Fill All Required Fields!');
        } else {

            this.handleSubmit(event);
            console.log(event);

        }
    }

    render() {
        let mainContent = null;
        let dropDownAllCountries = null;
        let dropDownAllCountriesCode = null;

        const currentDate = new Date();
        const day = currentDate.getDate();
        const month = currentDate.toLocaleString('default', { month: 'short' });
        const year = currentDate.getFullYear();
        const formattedDate = `${day}-${month}-${year}`;


        if (this.responseD) {
            dropDownAllCountries = this.allCountries.map((d: any) => {
                return (
                    <option value={d.name} key={d.id}>{d.name}</option>
                )
            })
            dropDownAllCountriesCode = this.allCountries.map((d: any) => {
                return (
                    <option value={d.phone_prefix} key={d.id}>{d.phone_prefix}</option>
                )
            })
        }

        if (this.opened) {
            mainContent = [
                <div id="main-container">
                    <h3>Guest Details</h3>
                    <div id="form-container">
                        <div>
                            <form action="">
                                <div>
                                    <p id="rg-date">Registration date : <span id="a">{formattedDate}</span></p>
                                </div>
                                <div class="input-container">
                                    <label htmlFor="firstName">First Name*</label>
                                    <input
                                        type="text"
                                        id="firstName"
                                        placeholder=""
                                        value={this.firstname}
                                        onChange={(event) => this.firstname = (event.target as HTMLInputElement).value} />
                                </div>
                                <div>
                                    {this.showFormError && this.firstname.trim() === '' ? <p class="error-message">Please Fill This Field.</p> : null}
                                </div>
                                <div class="input-container">
                                    <label htmlFor="lastname">Last Name*</label>
                                    <input
                                        type="text"
                                        id="lastName"
                                        placeholder=""
                                        value={this.lastname}
                                        onChange={(event) => this.lastname = (event.target as HTMLInputElement).value}
                                    />
                                </div>
                                <div>
                                    {this.showFormError && this.lastname.trim() === '' ? <p class="error-message">Please Fill This Field.</p> : null}
                                </div>
                                <div class="input-container">
                                    <label htmlFor="email">Email*</label>
                                    <input
                                        type="email"
                                        id="email"
                                        placeholder=""
                                        value={this.email}
                                        onChange={(event) => this.email = (event.target as HTMLInputElement).value}
                                    />
                                </div>
                                <div>
                                    {this.showFormError && this.email.trim() === '' ? <p class="error-message">Please Fill This Field.</p> : null}
                                </div>
                                <div class="input-container">
                                    <label htmlFor="email">Alternate email</label>
                                    <input
                                        type="email"
                                        id="Alt-email"
                                        placeholder=""
                                        value={this.alternateemail}
                                        onChange={(event) => this.alternateemail = (event.target as HTMLInputElement).value}
                                    />
                                </div>
                                <div class="input-container">
                                    <label htmlFor="password">Password*</label>
                                    <input
                                        type="password"
                                        id="password"
                                        placeholder=""
                                        value={this.password}
                                        onChange={(event) => this.password = (event.target as HTMLInputElement).value}
                                    />
                                </div>
                                <div>
                                    {this.showFormError && this.password.trim() === '' ? <p class="error-message">Please Fill This Field.</p> : null}
                                </div>
                                <div class="input-container">
                                    <label id="country-label" htmlFor="country">Country*</label>
                                    <select
                                        class="country-dropdown"
                                        id="country"
                                        onChange={(event) => this.country = (event.target as HTMLSelectElement).value}
                                    >
                                        <option value="" disabled selected>Select Your Country</option>
                                        {dropDownAllCountries}

                                    </select>
                                </div>
                                <div>
                                    {this.showFormError && this.country.trim() === '' ? <p class="error-message">Please Fill This Field.</p> : null}
                                </div>
                                <div class="input-container">
                                    <label htmlFor="city">City</label>
                                    <input
                                        type="text"
                                        id="city"
                                        placeholder=""
                                        value={this.city}
                                        onChange={(event) => this.city = (event.target as HTMLInputElement).value}
                                    />
                                </div>
                                <div class="input-container">
                                    <label id="address-lable" htmlFor="address">Address</label>
                                    <textarea
                                        class=""
                                        id="address-textarea"
                                        rows={2}
                                        placeholder=""
                                        value={this.adress}
                                        onInput={(event) => (this.adress = (event.target as HTMLTextAreaElement).value)}
                                    ></textarea>
                                </div>
                                <div class="input-container">
                                    <label id="countryCode-label" htmlFor="countryCode">Mobile</label>
                                    <select
                                        id="countryCode"
                                        onChange={(event) => (this.countrycode = (event.target as HTMLSelectElement).value)}
                                    >
                                        <option value="" disabled selected>Code</option>
                                        {dropDownAllCountriesCode}
                                    </select>
                                    <br />
                                </div>
                                <div>
                                    {this.showFormError && this.countrycode.trim() === '' ? <p class="error-message">Please Fill This Field.</p> : null}
                                </div>
                                <div class="">
                                    <input
                                        type="tel"
                                        id="mobileNumber"
                                        placeholder=""
                                        value={this.mobilenumber}
                                        onChange={(event) => this.mobilenumber = (event.target as HTMLInputElement).value}
                                    />
                                </div>
                                <div>
                                    {this.showFormError && this.mobilenumber.trim() === '' ? <p class="error-message">Please Fill This Field.</p> : null}
                                </div>
                                <div class="checkbox">
                                    <input
                                        type="checkbox"
                                        id="termsCheckbox"
                                        name="termsCheckbox"
                                        onChange={(event) => this.handleCheckboxChange(event)}
                                    />
                                    <label htmlFor="termsCheckbox">Newsletter</label>
                                </div>
                                <div>
                                    <span>Last used :</span>
                                    <span>Language: <span id="b">EN</span></span>
                                    <span>--- Currency: <span id="c">€</span></span>
                                </div>

                                <div class="line-separator"></div>

                                <button
                                    type="submit"
                                    class="full-width-button"
                                    onClick={(event) => this.saveGuestDetailsClick(event)}
                                >
                                    Save
                                </button>
                            </form>
                        </div>
                    </div>
                </div>
            ]
        }
        return (

            mainContent

        )
    }
}